<template>
    <div>
        <el-col class="intro">
            <el-row type="flex" class="title">
                <el-col>Introdução</el-col>
            </el-row>
            <el-row class="info" type="flex">
                <el-col>Para realizar o pedido do lanche para seu filho, é simples:</el-col>
                <el-col>1. Você seleciona qual o produto e indica a quantidade.</el-col>
                <el-col>2. Você pode selecionar outro tipo de produto e a quantidade.</el-col>
                <el-col>3. Você visualiza tudo o que selecionou com o preço total.</el-col>
                <el-col>Clique em finalizar, ou utilize a opção de voltar para alterar algum produto.</el-col>
                <el-col>
                    <el-checkbox-group v-model="filhosCheck">
                        <el-checkbox v-for="filho in listaFilhos" :label="filho">{{filho}}</el-checkbox>
                    </el-checkbox-group>
                </el-col>
            </el-row>
        </el-col>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                listaFilhos: ['filho1', 'filho2', 'filho3'],
                filhosCheck: []
            }
        },
        watch: {
            filhosCheck() {
                this.$store.dispatch('changeFilhosPedidos', this.filhosCheck);
            }
        }
    }
</script>
<style scoped>
    .title {
        margin-bottom: 1.5rem;
        text-align: center;
    }
    .intro {
        align-items: center;
        font-size: 1.25rem;
       
    }
    .info {
        flex-direction: column;
    }
    .info > div {
        margin-bottom: 0.75rem;
        font-size: 1rem;
        padding: 0 15px;
    }
</style>


